
@import "../../variables";

.EmojiReactions {
  display: flex;
  margin-top: 0.25em;
  flex-wrap: wrap;

  .emoji-reaction {
    padding: 0 0.5em;
    margin-right: 0.5em;
    margin-top: 0.5em;
    display: flex;
    align-items: center;
    justify-content: center;
    box-sizing: border-box;

    .reaction-emoji {
      width: 1.25em;
      margin-right: 0.25em;
    }

    &:focus {
      outline: none;
    }

    &.not-clickable {
      cursor: default;

      &:hover {
        box-shadow: $fallback--buttonShadow;
        box-shadow: var(--buttonShadow);
      }
    }

    &.-picked-reaction {
      border: 1px solid var(--accent, $fallback--link);
      margin-left: -1px; // offset the border, can't use inset shadows either
      margin-right: calc(0.5em - 1px);
    }
  }

  .emoji-reaction-expand {
    padding: 0 0.5em;
    margin-right: 0.5em;
    margin-top: 0.5em;
    display: flex;
    align-items: center;
    justify-content: center;

    &:hover {
      text-decoration: underline;
    }
  }
}
